import React, { Component, useState } from 'react';
import PropTypes from "prop-types";

import { scaleTime } from "d3-scale";
import { utcDay } from "d3-time";

import { format } from "d3-format";

import { ChartCanvas, Chart } from "react-stockcharts";
import {  BarSeries,
  AreaSeries,
  CandlestickSeries,
  LineSeries, } from "react-stockcharts/lib/series";

import { XAxis, YAxis } from "react-stockcharts/lib/axes";
import {
  CrossHairCursor,
  EdgeIndicator,
  CurrentCoordinate,
  MouseCoordinateX,
  MouseCoordinateY,
} from "react-stockcharts/lib/coordinates";

import { discontinuousTimeScaleProvider } from "react-stockcharts/lib/scale";
import {
  OHLCTooltip,
  MovingAverageTooltip,
} from "react-stockcharts/lib/tooltip";
import { ema, sma } from "react-stockcharts/lib/indicator";
import { fitWidth } from "react-stockcharts/lib/helper";
import { last, timeIntervalBarWidth } from "react-stockcharts/lib/utils";

class CodeChart extends Component {
    constructor(props){
        super(props)
    }
    componentDidMount(){

    }

    componentDidUpdate(prevProps, prevState, snapshot){
       if(prevProps.code !== this.props.code){
            //console.log("CodeChart componentDidupdate", this.props.code);
            //let api_url = process.env.REACT_APP_STOCK_API_URL+"/codes/"+this.props.code+"/sortflag/asc/price";
            //fetch(api_url)
            //    .then(res => res.json())
            //    .then(data =>{
            //          this.setState({data : data["price_list"]})
            //   });
        }
    }

    render(){
        console.log(this.props)
        const ema5 = ema()
          .id(0)
          .options({ windowSize: 5 })
          .merge((d, c) => { d.ema5 = c; })
          .accessor(d => d.ema5);

        const ema20 = ema()
          .id(2)
          .options({ windowSize: 20 })
          .merge((d, c) => { d.ema20 = c; })
          .accessor(d => d.ema20);

        const smaVolume50 = sma()
          .id(3)
          .options({ windowSize: 50, sourcePath: "volume" })
          .merge((d, c) => { d.smaVolume50 = c; })
          .accessor(d => d.smaVolume50);

        const { type, data:initialData, width, ratio } = this.props;
        const calculatedData = smaVolume50(ema20(ema5(initialData)));
        const xScaleProvider = discontinuousTimeScaleProvider.inputDateAccessor(d => d.date);

        const {
          data,
          xScale,
          xAccessor,
          displayXAccessor,
        } = xScaleProvider(calculatedData);

        const start = xAccessor(last(data));
        const end = xAccessor(data[Math.max(0, data.length - 50)]);
        const xExtents = [start, end];

        return (
          <div>
            { this.props.data.length > 0 ? (
            <ChartCanvas height={700}
              ratio={ratio}
              width={width * 2}
              margin={{ left: 50, right: 50, top: 10, bottom: 30 }}
              type={type}
              seriesName="Chart"
              data={data}
              xAccessor={xAccessor}
              displayXAccessor={displayXAccessor}
              xScale={scaleTime()}
              xExtents={xExtents}>

            <Chart id={1}
              yExtents={[d => [d.high, d.low], ema20.accessor(), ema5.accessor()]}
              padding={{ top: 10, bottom: 20 }}
            >
              <XAxis axisAt="bottom" orient="bottom"/>
              <YAxis axisAt="right" orient="right" ticks={5} />
              <MouseCoordinateY
                at="right"
                orient="right"
                displayFormat={format(".1f")} />

              <CandlestickSeries />
              <LineSeries yAccessor={ema20.accessor()} stroke={ema20.stroke()}/>
              <LineSeries yAccessor={ema5.accessor()} stroke={ema5.stroke()}/>

              <CurrentCoordinate yAccessor={ema20.accessor()} fill={ema20.stroke()} />
              <CurrentCoordinate yAccessor={ema5.accessor()} fill={ema5.stroke()} />

              <OHLCTooltip origin={[-40, 0]}/>
              <MovingAverageTooltip
                onClick={e => console.log(e)}
                origin={[-38, 15]}
                options={[
                  {
                    yAccessor: ema20.accessor(),
                    type: "EMA",
                    stroke: ema20.stroke(),
                    windowSize: ema20.options().windowSize,
                  },
                  {
                    yAccessor: ema5.accessor(),
                    type: "EMA",
                    stroke: ema5.stroke(),
                    windowSize: ema5.options().windowSize,
                  }
                ]}
              />

            </Chart>
          </ChartCanvas>
          ) : (null)
        }
        </div>

        );
    }
}

CodeChart.propTypes = {
  data: PropTypes.array.isRequired,
  width: PropTypes.number.isRequired,
  ratio: PropTypes.number.isRequired,
  type: PropTypes.oneOf(["svg", "hybrid"]).isRequired,
};

CodeChart.defaultProps = {
  type: "svg",
};
CodeChart = fitWidth(CodeChart);

export default CodeChart